router.beforeEach((to,from,next)=>{
  if (to.path == '/') {
    next();
  } else {
    if (window.sessionStorage.getItem("user")) {
      initMenu(router, store);
      next();
    } else {
      //å½“ä½ è¿˜æ²¡ç™»å½•è®¿é—®æŽ¥å£æ—¶,å¤„ç†ä½ å®Œæˆç™»å½•åŽç›´æŽ¥é‡å®šå‘ä½ æ²¡ç™»å½•å‰è®¿é—®çš„æŽ¥å£ä¸Š
      //initMenu(router, store);
      next('/?redirect=' + to.path);
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
